@import url(https://fonts.googleapis.com/css2?family=Akaya+Kanadaka&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.navbar{
    background: white;
    margin-top: 1%;
    display: flex;
    flex-wrap: nowrap;
    height: 80px;
    width: 100%;
    height: 50px;

}

.navbar-title{
    font-family: 'Akaya Kanadaka', cursive;
    padding-top: 8px;
    font-size: calc(12px + 1.5vw);
    text-align: center;
    margin-left: 20px;
    font-weight: 900;
    padding: 2%;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 2px 4px rgba(37, 13, 53, 0.25);
    color: rgb(5, 78, 121) ;
}

.title-span{
    font-size: calc(12px + 0.5vw);
    margin-left: 2px;
    color: rebeccapurple;
    font-weight: 400;
}



body{
    min-width: 100%;
    min-height: 100%;
    justify-content: justify;
  }
  
  .id-image{
    display: block;
    flex-direction: column;
    flex-wrap: wrap;
    min-width: 30%;
    height: 100%;
  }
  
  .Image{
    grid-gap: 1.5%;
    gap: 1.5%;
    margin: auto;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    height: 50%;
  }
  
  .loader{
    justify-content: center;
    align-items: center;
    display: flex;
    width: 100%;
    height: 100%;
  }
  
  img{
    width: 450px;
    margin-bottom: 1%;
    height: 290px;
    padding-top: 3%;
  }
  
  
  .text-data{
    text-align: center;
    font-size: 16px;
    font-family: 'Akaya Kanadaka', cursive;
    color: rgba(0, 0, 0, 0.8);
  }

  .credits{
  color: rgb(0, 162, 255) ;
  }

  .tags{
    color: rgba(0, 0, 0, 0.5);
    font-size: 14px;
  }
  .search-bar{
    width: 20%;
    justify-self: right;
    display: flex;
    margin: 0 0 75px;
    margin-bottom: 3%;
    margin-left: 75%;
    margin-top: -2.2%;
  }
  
  .search-box{
  display: block;
  width: 100%;
  padding: 15px;
  -webkit-appearance: none;
          appearance: none;
  background: transparent none repeat 0 0 / auto auto padding-box border-box scroll;
  background: initial;
  outline: none;
  border: none;
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 0 0 16px 16px;
  margin-top: -25px;
  font-size: 20px;
  box-shadow: 0 5px rgba(46, 39, 39, 0.25);
  transition: 0.5s ease-in;
  }
  
  .search-box::-webkit-input-placeholder{
    color: rgba(21, 89, 167, 0.3);
    font-family: 'Times New Roman', Times, serif;
  }
  
  .search-box:-ms-input-placeholder{
    color: rgba(21, 89, 167, 0.3);
    font-family: 'Times New Roman', Times, serif;
  }
  
  .search-box::placeholder{
    color: rgba(21, 89, 167, 0.3);
    font-family: 'Times New Roman', Times, serif;
  }
  
  .search-box:hover{
    background-color: rgba(255, 255, 255, 0.7);
  }
  
  
  @media screen and (max-width: 999px) {
    .search-bar{
      margin-top: -4.2%;
    }
  }

